<template>
  <div class="pager-wrapper">
    <a @click="changeCurrent(1)" :class="{ disabled: current === 1 }"></a>
    <a @click="changeCurrent(current - 1)" :class="{ disabled: current === 1 }"
      ></a
    >
    <a
      @click="changeCurrent(n)"
      v-for="(n, i) in numbers"
      :key="i"
      :class="{ active: current === n }"
      >{{ n }}</a
    >
    <a
      @click="changeCurrent(current + 1)"
      :class="{ disabled: current === calculatePage }"
      ></a
    >
    <a
      @click="changeCurrent(calculatePage)"
      :class="{ disabled: current === calculatePage }"
      ></a
    >
    <div class="jump-wrapper">
      <span>{{ calculatePage }},</span>
      <input type="number" :max="calculatePage" :min="1" ref="jumpNumber" />
      <span></span>
    </div>
    <a @click="clickJump"></a>
  </div>
</template>

<script lang="js">
export default {
    props:{
        //
        current:{
            type:Number,
            required:true
        },
        //
        total:{
            type:Number,
            required:true
        },
        //
        pageTotal:{
            type:Number,
            default:10
        },
        //
        visibleNumber:{
          type:Number,
          default:5
        }
    },
    computed:{
        //
        calculatePage(){
            return Math.ceil(this.total / this.pageTotal);
        },
        //
        visibleMin(){
            let min = this.current - Math.ceil(this.visibleNumber/2);
            if(min < 1){
                min = 1
            }
            return min;
        },
        //
        visibleMax(){
            let max = this.visibleMin + this.visibleNumber -1;
            if(max > this.calculatePage){
                max = this.calculatePage;
            }
            return max;
        },
        //
        numbers(){
            let list = [];
            for(let i = this.visibleMin;i <= this.visibleMax;i++){
                list.push(i);
            }
            return list;
        }
    },
    methods:{
        changeCurrent(currentNumber){
            if(currentNumber === 0){
                return
            }
            if(currentNumber > this.calculatePage){
                return
            }
            if(currentNumber === this.current){
                return
            }
            this.$emit('changeCurrent', currentNumber)
        },
        clickJump(){
          const jumpNumber = parseInt(this.$refs.jumpNumber.value);
          if(jumpNumber < 1){
            return
          }
          if(jumpNumber > this.calculatePage){
            return
          }
          this.$emit('changeJump', jumpNumber);

        }
    }
}
</script>

<style lang="less" scoped>
@import "~@/style/var.less";
.pager-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  .jump-wrapper {
    display: flex;
    align-items: center;
    margin-left: 5px;
  }
}
a {
  padding: 5px;
}
.active {
  color: @dark;
  font-weight: bold;
  cursor: default;
}
.disabled {
  color: @gray;
  cursor: not-allowed;
}
</style>
